export default {
	data() {
		return {
			index1: -1, index2: -1,
			picker1: ['小学', '初中', '高中', '中专', '大专', '本科', '硕士', '博士', '博士后', 'MBA'],
			picker2: ['统招', '非统招’'],
			date1: '2018-12-25',
			date2: '2018-12-25',
			
			schoolInfo: {
				schoolName: "",
				fromTime: "",
				toTime: ""
			}
		}
	},
	methods: {
		store() {
			this.schoolInfo.fromTime = this.date1;
			this.schoolInfo.toTime = this.date2;
			
			wx.setStorage({
				key:"schoolInfo",
				data: JSON.stringify(this.schoolInfo)
			})
		},
		zNavigate(id) {
			uni.navigateTo({
				url:`/pages/${id}`
			});
			this.clicked = true;
		},
		
		PickerChange1(e) {
			this.index1 = e.detail.value
		},
		PickerChange2(e) {
			this.index2 = e.detail.value
		},
		
		DateChange1(e) {
			this.date1 = e.detail.value
		},
		DateChange2(e) {
			this.date2 = e.detail.value
		}
	}
}
